var React = require('react');
var PokemonRow = require('./PokemonRow.react');

class PokedexTable extends React.Component{
    constructor() {
        super();
        this.render = this.render.bind(this);
      }
    render() {
        var rows = [];
        this.props.pokemon.forEach( pokemon => {
            if(pokemon.name.indexOf(this.props.filterText) === -1){
                return;
            }
            rows.push(React.createElement(PokemonRow, {pokemon: pokemon, key: pokemon.name}));
        });
        return (React.createElement("div", {className: "main"}, rows));
    }
};

module.exports = PokedexTable;